<template>
	<div>
		<Chart :cdata="cdata" />
	</div>
</template>

<script>
	import Chart from './chart.vue'
	export default {
		data() {
			return {
				drawTiming: null,
				cdata: [],
				name: ['TNT', '硝酸', '硫酸', '甲苯', '乙醇'],
				colorList: ['#ff0000', '#ffd700', '#00FFFF', '#A020F0', '#32CD32', '#DA70D6']
			}
		},
		components: {
			Chart,
		},
		mounted() {
			this.drawTimingFn();
		},
		beforeDestroy() {
			clearInterval(this.drawTiming);
		},
		methods: {
			drawTimingFn() {
				this.setData();
				this.drawTiming = setInterval(() => {
					this.setData();
				}, 3000);
			},
			setData() {
				let num = Math.floor(Math.random() * (50 - 1 + 1) + 1);
				this.cdata = []
				for (let k = 0; k < 5; k++) {
					this.cdata.push({
						name: this.name[k],
						value: num + (k + 1) * 20,
						itemStyle: {
							color: this.colorList[k],
						}
					})
				}
			}
		}
	};
</script>

