<template>
  <div>
    <Echart
      :options="options"
      id="riskchart"
      height="180px"
    ></Echart>
  </div>
</template>

<script>
  import Echart from '@/common/echart'

  export default {
    data() {
      return {
        options: {},
      };
    },
    components: {
      Echart,
    },
    props: {
      cdata: {
        type: Object,
        default: () => ({})
      },
    },
    watch: {
      cdata: {
        handler(newData) {
          this.options = {
            backgroundColor: '',
            series: [
              {
                name: '业务指标',
                type: 'gauge',
                radius: '100%',
								center: ["50%", "65%"], // 仪表位置
                min: 0,
                max: 100,
                splitNumber: 18,
                detail: {
                  valueAnimation: true,
                  formatter: '{value}',
                  color: 'auto'
                },
                axisTick: {
                  lineStyle: {
                    color: "#1e2e51"
                  }
                },
                splitLine: {
                  lineStyle: {
                    width: 1,
                    color: "#1e2e51"
                  }
                },
                pointer: {
                  itemStyle: {
                    color: 'auto'
                  }
                },
                axisLabel: {
                  formatter: function (e) {
                    if (e < 20) {
                      return ""
                    } else if (e < 25) {
                      return "低"
                    } else if (e < 50) {
                      return ""
                    } else if (e < 61) {
                      return ""
                    } else if (e < 62) {
                      return "中"
                    } else if (e < 67) {
                      return ""
                    } else if (e < 77) {
                      return ""
                    } else if (e < 78) {
                      return ""
                    } else if (e < 84) {
                      return ""
                    } else if (e < 90) {
                      return "高"
                    } else if (e < 95) {
                      return ""
                    } else {
                      return ""
                    }
                  },
                  fontSize: 18
                },
                axisLine: {
                  lineStyle: {
                    color: [[0.5, "#5FDAFF"], [0.75, "#fff866"], [1.0, "#ff641c"]],
                    width: 14
                  }
                },
                data: [
                  {
                    value: newData.seriesData,
                    name: '',
                  },
                ]
              }
            ]
          }
        },
        immediate: true,
        deep: true
      }
    }
  };
</script>

