<template>
	<div>
		<Echart :options="options" id="centreLeft1Chart" height="200px"></Echart>
	</div>
</template>

<script>
	import Echart from '@/common/echart'
	export default {
		data() {
			return {
				options: {},
			};
		},
		components: {
			Echart,
		},
		props: {
			cdata: {
				type: Array,
				default: () => ([])
			},
		},
		watch: {
			cdata: {
				handler(newData) {
					this.options = {
						tooltip: {
							trigger: 'axis',
							axisPointer: {
								type: 'shadow' // 'shadow' as default; can also be 'line' or 'shadow'
							}
						},
						legend: {
							show:false
						},
						grid: {
							top:10,
							left: '3%',
							right: 50,
							bottom: '3%',
							containLabel: true
						},
						xAxis: {
							type: 'value'
						},
						yAxis: {
							type: 'category',
							data: ['地区A', '地区B', '地区C', '地区D', '地区E']
						},
						series: [{
							name: 'Direct',
							type: 'bar',
							stack: 'total',
							label: {
								show: true,
								position: 'right',
								fontSize:16,
								formatter: (val) => {
									return `${val.data.value}(${val.data.name})`
								}
							},
							emphasis: {
								focus: 'series'
							},
							barWidth:20,
							itemStyle: {
								normal:{
										barBorderRadius:[0,20,20,0],
								}
							
							},
							data: newData
						}]
					}
				},
				immediate: true,
				deep: true
			}
		}
	};
</script>
