<template>
  <div>
    <Chart :cdata="cdata" />
  </div>
</template>

<script>
import Chart from './chart.vue'
export default {
  data () {
    return {
      drawTiming: null,
      cdata: {
        weekCategory: [],
        weekLineData1: [],
        weekLineData2: [],
      }
    }
  },
  components: {
    Chart,
  },
  mounted () {
    this.drawTimingFn();
  },
  beforeDestroy () {
    clearInterval(this.drawTiming);
  },
  methods: {
    drawTimingFn () {
      this.setData();
      this.drawTiming = setInterval(() => {
        this.setData();
      }, 6000);
    },
    setData () {
      // 清空轮询数据
      this.cdata.weekCategory = [];
      this.cdata.weekLineData1 = [];
      this.cdata.weekLineData2 = [];

      // 周数据
      for (let i = 0; i < 7; i++) {
        // 日期
        let date = new Date();
        this.cdata.weekCategory.unshift([date.getMonth() + 1, date.getDate()-i].join("/"));

        // 折线图数据
        let distance1 = Math.round(Math.random() * 11000 + 500);
        let distance2 = Math.round(Math.random() * 15000 + 500);
        this.cdata.weekLineData1.push(distance1);
        this.cdata.weekLineData2.push(distance2);

      }

    }
  }
};
</script>

